/* styles */
.hcms-settings-header {
  margin: 24px 0 36px 0; }

.hcms-hero-img-section input:focus,
.hcms-srch-back-img-section input:focus {
  outline: none; }

.hcms-hero-img-section .hcms-hero-img,
.hcms-srch-back-img-section .hcms-hero-img {
  display: none;
  margin-bottom: 10px; }

.hcms-hero-img-section .hcms-hero-remove-btn,
.hcms-srch-back-img-section .hcms-hero-remove-btn {
  display: none;
  color: #0071a1;
  border-color: #0071a1;
  background: #f3f5f6;
  vertical-align: top;
  text-decoration: none;
  font-size: 13px;
  line-height: 2;
  min-height: 30px;
  margin: 0;
  padding: 0 10px;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  -webkit-appearance: none;
  border-radius: 3px;
  white-space: nowrap;
  box-sizing: border-box; }

.hcms-hero-img-section .hcms-hero-upload-btn,
.hcms-srch-back-img-section .hcms-hero-upload-btn {
  width: 200px;
  position: relative;
  text-align: center;
  border: 1px dashed #b4b9be;
  box-sizing: border-box;
  padding: 9px 0;
  line-height: 1.6;
  cursor: pointer;
  background-color: #edeff0;
  color: #32373c; }

.hcms-hero-img-section.uploaded .hcms-hero-img,
.hcms-srch-back-img-section.uploaded .hcms-hero-img {
  display: block; }

.hcms-hero-img-section.uploaded .hcms-hero-remove-btn,
.hcms-srch-back-img-section.uploaded .hcms-hero-remove-btn {
  display: inline-block; }

.hcms-hero-img-section.uploaded .hcms-hero-upload-btn,
.hcms-srch-back-img-section.uploaded .hcms-hero-upload-btn {
  width: auto;
  color: #0071a1;
  border-color: #0071a1;
  background: #f3f5f6;
  vertical-align: top;
  display: inline-block;
  text-decoration: none;
  font-size: 13px;
  line-height: 2;
  min-height: 30px;
  margin: 0 8px 0 0;
  padding: 0 10px;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  -webkit-appearance: none;
  border-radius: 3px;
  white-space: nowrap;
  box-sizing: border-box; }
